import React, { useState, useEffect } from "react";
import {
  StyleSheet,
  View,
  FlatList,
  SafeAreaView,
  Text,
  Image,
  TouchableOpacity,
} from "react-native";
// import "../assets/css/fonts.css";

const styles = StyleSheet.create({
  itemContainer: {
    height: 150,
    width: "100%",
    borderColor: "gray",
    borderWidth: 1,
    flexDirection: "row",
    padding: 10,
    backgroundColor: "rgba(255, 255, 255, 0.1)",
  },
  leftContainer: {
    width: 100,
  },
  rightContainer: {
    flex: 1,
    padding: 10,
    justifyContent: "space-around",
    // alignItems: "center",
  },
  text: {
    fontSize: 24,
    fontWeight: "bold",
    // fontFamily: "Noto Sans JP",
  },
  subText: {
    fontSize: 20,
    fontWeight: "bold",
    // fontFamily: "Noto Sans JP",
    color: "dimgray",
  },
});

function ListItem(props) {
  if (!!props.imageUrl) {
    var url = props.imageUrl.thumbnail;
    if (url.indexOf("http:") >= 0) {
      var newUrl = url.replace("http:", "https:");
      url = newUrl;
    }
  }
  console.log(url);
  return (
    <TouchableOpacity style={styles.itemContainer} onPress={props.onPress}>
      <View style={styles.leftContainer}>
        {!!props.imageUrl && (
          <Image style={{ width: 100, height: 130 }} source={url}></Image>
        )}
      </View>
      <View style={styles.rightContainer}>
        <Text numberOfLines={2} style={styles.text}>
          {props.title}
        </Text>
        <Text numberOfLines={1} style={styles.subText}>
          {props.author}
        </Text>
      </View>
    </TouchableOpacity>
  );
}
export default ListItem;
