import React from "react";
import {
  StyleSheet,
  SafeAreaView,
  Text,
  Image,
  View,
  TouchableOpacity,
  ScrollView,
  Linking,
} from "react-native";

const styles = StyleSheet.create({
  articleContainer: {
    backgroundColor: "rgba(255,255,255,0.7)",
    margin: 20,
    padding: 40,
  },
  thumbnail: {
    height: 400,
    width: "100%",
    paddingHorizontal: 30,
    // backgroundColor: "whitesmoke",
  },
  details: {
    marginTop: 10,
    fontSize: 24,
    color: "gray",
  },
  Content: {
    fontSize: 24,
    fontWeight: "bold",
    paddingHorizontal: 20,
  },
});

export default function ArticleScreen({ route }) {
  const { article } = route.params;
  console.log(article);
  if (!!article.volumeInfo.imageLinks) {
    //   console.log(article.volumeInfo.title);
    // console.log(article.volumeInfo.imageLinks);
    var url = article.volumeInfo.imageLinks.thumbnail;
    if (url.indexOf("http:") >= 0) {
      var newUrl = url.replace("http:", "https:");
      url = newUrl;
    }
    console.log("url=" + url);
  }
  return (
    <ScrollView showsVerticalScrollIndicator={false}>
      <View style={styles.articleContainer}>
        <View style={styles.thumbnail}>
          {!!article.volumeInfo.imageLinks && (
            <Image style={{ width: 300, height: 400 }} source={url}></Image>
          )}
        </View>
        <Text style={styles.details}>{""}</Text>
        <Text style={styles.details}>{"Title:"}</Text>
        <Text style={styles.Content}>{article.volumeInfo.title}</Text>
        <Text style={styles.details}>{"Authors:"}</Text>
        <Text style={styles.Content}>{article.volumeInfo.authors}</Text>
        <Text style={styles.details}>{"Publisher:"}</Text>
        <Text style={styles.Content}>{article.volumeInfo.publisher}</Text>
        <Text style={styles.details}> {"PublishedDate:"}</Text>
        <Text style={styles.Content}>{article.volumeInfo.publishedDate}</Text>
      </View>
    </ScrollView>
  );
}
