import React from "react";
import { StyleSheet, Text, View, Image, ImageBackground } from "react-native";
import { NavigationContainer, DefaultTheme } from "@react-navigation/native";
import { createStackNavigator } from "@react-navigation/stack";
import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";
import ReactScreen from "../screens/ReactScreen";
import VueScreen from "../screens/VueScreen";
import AngularScreen from "../screens/AngularScreen";
import ArticleScreen from "../screens/ArticleScreen";

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: "column",
  },
  background: {
    flex: 1,
  },
  image: {
    flex: 1,
    resizeMode: "cover",
    justifyContent: "center",
  },
});

const Tab = createBottomTabNavigator();
const Stack = createStackNavigator();

function ReactStack() {
  return (
    <Stack.Navigator>
      <Stack.Screen
        name="Books about 'React'"
        component={ReactScreen}
        // options={{ headerShown: false }}
      />
      <Stack.Screen name="Article" component={ArticleScreen} />
    </Stack.Navigator>
  );
}

function VueStack() {
  return (
    <Stack.Navigator>
      <Stack.Screen
        name="Books about 'Vue'"
        component={VueScreen}
        // options={{ headerShown: false }}
      />
      <Stack.Screen name="Article" component={ArticleScreen} />
    </Stack.Navigator>
  );
}

function AngularStack() {
  return (
    <Stack.Navigator>
      <Stack.Screen
        name="Books about 'Angular'"
        component={AngularScreen}
        // options={{ headerShown: false }}
      />
      <Stack.Screen name="Article" component={ArticleScreen} />
    </Stack.Navigator>
  );
}

const MyTheme = {
  ...DefaultTheme,
  colors: {
    ...DefaultTheme.colors,
    primary: "rgb(255,255,255)",
    card: "rgb(0, 0, 128)",
    text: "ghostwhite",
    border: "rgb(199, 199, 204)",
    background: "rgba(255,255,255,0.6)",
  },
};

export default function AppNavigaotor() {
  return (
    <View style={styles.container}>
      <ImageBackground
        source={require("../assets/Apple.png")}
        style={styles.image}
      >
        <NavigationContainer theme={MyTheme}>
          <Tab.Navigator>
            <Tab.Screen name="React" component={ReactStack} />
            <Tab.Screen name="Vue" component={VueStack} />
            <Tab.Screen name="Angular" component={AngularStack} />
          </Tab.Navigator>
        </NavigationContainer>
      </ImageBackground>
    </View>
  );
}
